import { createStore } from 'vuex'
import { get } from '../utils/request'
export default createStore({
  state: {
    listIndex: 0,
    scollElement: 0,
    leftMenIndex: 1,
    isLogin: false,
    userName: '',
    articleIndex: '',
    lycontent: []
  },
  mutations: {
    changeIndex (state, payload) {
      const { listIndex } = payload
      state.listIndex = listIndex
    },
    changeMenIndex (state, payload) {
      state.leftMenIndex = payload
    },
    changeIsLogin (state, payload) {
      state.isLogin = true
      localStorage.isLogin = state.isLogin
    },
    ChangeUserName (state, payload) {
      state.userName = payload
      localStorage.userName = state.userName
    },
    changeFiyIndex (state, payload) {
      state.articleIndex = payload
      console.log(state.articleIndex)
    },
    preserLy (state, payload) {
      state.lycontent = payload
      localStorage.lycontent = JSON.stringify(state.lycontent)
    },
    ChangeSetLyTexr (state, payload) {
      state.lycontent.unshift(payload)
      localStorage.lycontent = JSON.stringify(state.lycontent)
    }
  },
  actions: {
    getlyContent (context) {
      const getList = async () => {
        const result = await get('/api/ly/content', {})
        context.commit('preserLy', result)
      }
      getList()
    }
  },
  modules: {
  }
})
