<template>
  <div class="header">
    <div class="header_logo">wxw导航</div>
    <ul class="header_meun">
      <li
        @mouseenter="enter"
        @mouseleave="leave"
      >
        <router-link to="/">
          <span>方向分类</span>
          <i class="iconfont icon-below-s" />
        </router-link>
      </li>
      <li>
        <router-link to="/boke">
          <span>个人博客</span>
        </router-link>
      </li>
      <li>
        <router-link to="/ly">
          <span>在线留言</span>
        </router-link>
      </li>
      <li>
        <span>划水中</span>
      </li>
      <li>
        <span>划水中</span>
      </li>
    </ul>
    <div
      class="userInfo"
      v-if="islogin"
    >
      <span>{{ username }}</span>
    </div>
    <div
      class="header_user"
      v-else
    >
      <div class="header_user_content">
        <span @click="handleClickLogin">登录</span>
        <span @click="handleClickRegister">注册</span>
      </div>
    </div>
  </div>
  <show-menu
    v-show="isShowMenu"
    @mouseenter="enter"
    @mouseleave="leave"
  />
  <header-login
    v-show="isShowLogin"
    @clickShow="clickshowlogin"
  />
  <header-register v-show="isShowRegi" />
  <header-mock
    @click="handleClickMock"
    v-show="isShowMock"
  />
</template>

<script>
import { useStore } from 'vuex'
import { reactive, toRefs, watch, ref } from 'vue'
import ShowMenu from './ShowMenu.vue'
import HeaderLogin from '../login/HeaderLogin.vue'
import HeaderRegister from '../login/HeaderRegister.vue'
import HeaderMock from '../login/HeaderMock.vue'
// 导航菜单显示隐藏
const useShowLisEffect = () => {
  const isShow = reactive({
    isShowMenu: false
  })
  // 菜单栏逻辑
  const enter = () => {
    isShow.isShowMenu = true
  }
  const leave = () => {
    isShow.isShowMenu = false
  }
  const { isShowMenu } = toRefs(isShow)
  return {
    isShowMenu,
    enter,
    leave
  }
}
// 登录注册逻辑
const useShowLoginEffect = () => {
  // const store = useStore()
  const isShow = reactive({
    isShowLogin: false,
    isShowRegi: false,
    isShowMock: false
  })
  // 点击登录组件
  const handleClickLogin = () => {
    isShow.isShowMock = true
    isShow.isShowLogin = !isShow.isShowLogin
  }
  // 点击注册组件
  const handleClickRegister = () => {
    isShow.isShowMock = true
    isShow.isShowRegi = !isShow.isShowRegi
  }
  // 遮盖层逻辑
  const handleClickMock = () => {
    isShow.isShowMock = false
    isShow.isShowLogin = false
    isShow.isShowRegi = false
  }
  // 接受子组件的传值
  const clickshowlogin = e => {
    isShow.isShowMock = e.false
    isShow.isShowLogin = e.false
    isShow.isShowRegi = e.false
  }

  const { isShowLogin, isShowRegi, isShowMock } = toRefs(isShow)
  return {
    isShowLogin,
    isShowRegi,
    isShowMock,
    handleClickLogin,
    handleClickMock,
    handleClickRegister,
    clickshowlogin
  }
}
// 判断是否登录
const useisLogin = () => {
  const islogin = ref(false)
  const store = useStore()
  watch(
    () => store.state.isLogin,
    (val, old) => {
      islogin.value = val
    }
  )
  // 保留登录状态
  islogin.value = localStorage.isLogin
  return {
    islogin
  }
}
// 获取用户信息
const useUserInfo = () => {
  const username = ref('')
  const store = useStore()
  watch(
    () => store.state.userName,
    (val, old) => {
      username.value = val
    }
  )
  // 保留信息状态
  username.value = localStorage.userName
  return {
    username
  }
}
export default {
  name: 'Header',
  components: {
    ShowMenu,
    HeaderLogin,
    HeaderRegister,
    HeaderMock
  },
  setup(props) {
    const { isShowMenu, enter, leave } = useShowLisEffect()
    const { isShowRegi, isShowMock, isShowLogin, handleClickLogin, handleClickRegister, handleClickMock, clickshowlogin } = useShowLoginEffect()
    const { islogin } = useisLogin()
    const { username } = useUserInfo()
    return {
      username,
      islogin,
      isShowMenu,
      isShowLogin,
      isShowRegi,
      isShowMock,
      enter,
      leave,
      handleClickLogin,
      handleClickRegister,
      handleClickMock,
      clickshowlogin
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/index.scss';

.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
  font-size: 0.14rem;
  background-color: rgb(255, 255, 255);
  border-bottom: 0.01rem solid rgb(235, 235, 235);

  &_logo {
    float: left;
    width: 2rem;
    font-size: 0.4rem;
    text-align: center;
    cursor: default;
    // margin-left: 0.3rem;
  }

  &_meun {
    float: left;
    margin: 0;
    padding: 0;

    li {
      float: left;
      height: 100%;
      list-style: none;
      padding: 0 0.2rem;
      font-size: 0.18rem;
      cursor: pointer;

      span {
        color: #6b7184;
      }

      i {
        margin-left: 0.1rem;
        color: #6b7184;
      }

      &:hover {
        border-bottom: 0.01rem solid #aaa;
      }
    }
  }

  &_user {
    width: 2rem;
    height: 0.6rem;
    float: right;
    position: relative;

    &_content {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;

      span {
        border: 0.01rem solid #aaa;
        padding: 0.05rem 0.08rem;
        border-radius: 0.1rem;
        cursor: pointer;

        &:nth-child(1) {
          margin-right: 0.1rem;
        }

        &:hover {
          background-color: #6b7184;
          color: #fff;
        }
      }
    }
  }
}

.userInfo {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 0.4rem;
  height: 0.4rem;
  text-align: center;
  line-height: 0.4rem;
  border: 0.01rem solid #aaaa;
  border-radius: 50%;
  font-size: 0.12rem;
}
</style>
