<template>
  <div class="listcontent_warpper">
    <div class="warpper_content">
      <div
        class="content_item"
        v-for="item in list.content"
        :key="item"
      >
        <div class="item_title">{{item.name}}</div>
        <ul class="item_content">
          <template
            v-for="items in item.list"
            :key="items"
          >
            <li
              class="item_list"
              @click="See(items.tourl)"
            >
              <div class="item_list_head">
                <img
                  :src="items.imgurl"
                  alt
                />
                <span>{{items.name}}</span>
              </div>
              <div class="item_list_text">们是最好的课程我们是最好的课程</div>
            </li>
          </template>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { reactive, toRefs } from 'vue'
import { get } from '../../utils/request.js'
// 具体内容渲染
const useListEffect = () => {
  const store = useStore()
  const listContent = reactive({ list: {} })
  const getList = async () => {
    const result = await get('/api/menu', {})
    listContent.list = result[store.state.listIndex]
  }
  getList()
  const { list } = toRefs(listContent)
  return {
    list
  }
}
// 链接跳转
const useToLinkEffect = () => {
  const See = e => {
    window.open(e, '_blank')
  }
  return {
    See
  }
}
export default {
  name: 'ListContent',
  setup(props, context) {
    const { list } = useListEffect()
    const { See } = useToLinkEffect()
    return {
      list,
      See
    }
  }
}
</script>

<style lang="scss" scoped>
.listcontent_warpper {
  width: 100%;
  padding-bottom: 0.5rem;
  .warpper_content {
    width: 100%;
    height: 100%;
    .content_item {
      width: 100%;
      margin-bottom: 1rem;
      border-radius: 0.05rem;
      background-color: #fff;
      .item_title {
        height: 0.5rem;
        border-bottom: 0.01rem solid rgb(242, 242, 242);
        padding-left: 0.15rem;
        line-height: 0.5rem;
        font-size: 0.2rem;
      }
      .item_content {
        margin: 0;
        padding: 0.2rem;
        width: 100%;
        height: calc(100% - 0.51rem);
        box-sizing: border-box;
        padding-left: 0.3rem;
        padding-right: 0.1rem;
        &::after {
          content: '';
          display: block;
          clear: both;
        }
        .item_list {
          float: left;
          width: 2rem;
          height: 1rem;
          box-sizing: border-box;
          list-style: none;
          background-color: rgb(242, 242, 242);
          margin-bottom: 0.1rem;
          margin-right: 0.2rem;
          border-radius: 0.05rem;
          padding: 0.1rem 0.15rem;
          &_head {
            display: flex;
            align-items: center;
            margin-bottom: 0.1rem;
            img {
              width: 0.4rem;
              height: 0.4rem;
              border-radius: 50%;
              margin-right: 0.1rem;
            }
            span {
              font-size: 0.12rem;
              font-weight: 700;
              color: #30333c;
            }
          }
          &_text {
            width: 100%;
            height: calc(1rem - 0.6rem);
            font-size: 0.14rem;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            font-size: 0.16rem;
            color: #a1a7b7;
          }
          &:hover {
            background-color: #6b7184;
            span {
              color: #fff;
            }
          }
        }
      }
    }
  }
}
</style>
