<template>
  <div class="wrapper">
    <div class="wrapper_title">
      <span>{{list.name}}</span>
    </div>
    <ul class="wrapper_menu">
      <li
        class="wrapper_menu_item"
        v-for="(item,index) in list.content"
        :key="item"
        @click="handleClickMenIndex(index)"
      >
        <i class="iconfont icon-below-s"></i>
        <span>{{item.name}}</span>
      </li>
    </ul>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { reactive, toRefs } from '@vue/reactivity'
import { get } from '../../utils/request.js'
// 左边导航栏逻辑
const useLeftMenuEffect = () => {
  const store = useStore()
  const leftMenu = reactive({ list: {} })
  const getmenu = async () => {
    const result = await get('/api/menu', {})
    leftMenu.list = result[store.state.listIndex]
  }
  getmenu()
  const { list } = toRefs(leftMenu)
  return {
    list,
    getmenu
  }
}
// 修改state的值
const usechangeLeftIndex = () => {
  const store = useStore()
  const handleClickMenIndex = index => {
    store.commit('changeMenIndex', index)
  }
  return {
    handleClickMenIndex
  }
}
export default {
  name: 'LeftMenu',
  setup(props) {
    const { list, getmenu } = useLeftMenuEffect()
    const { handleClickMenIndex } = usechangeLeftIndex()
    return {
      list,
      getmenu,
      handleClickMenIndex
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/index.scss';
.wrapper {
  width: 2rem;
  height: calc(100vh - 0.61rem);
  background-color: rgb(255, 255, 255);
  overflow: auto;
  &_title {
    width: 100%;
    height: 0.5rem;
    box-sizing: border-box;
    border-bottom: 0.01rem solid rgb(235, 235, 235);
    cursor: default;
    span {
      display: block;
      padding-left: 0.15rem;
      font-size: 0.2rem;
      line-height: 0.5rem;
      color: #28b779;
    }
  }
  &_menu {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    overflow-y: auto;

    &_item {
      display: flex;
      align-items: center;
      width: 100%;
      box-sizing: border-box;
      padding: 0 0.15rem;
      cursor: pointer;
      color: #6b7184;
      &:hover {
        background-color: #6b7184;
        color: #fff;
      }
      i {
        margin-right: 0.15rem;
      }
      span {
        height: 0.3rem;
        line-height: 0.3rem;
        font-size: 0.14rem;
        letter-spacing: 0.01rem;
      }
    }
  }
}
</style>
