<template>
  <div class="rigthcontent_wrapper">
    <div class="content">
      <Search />
      <ListContent />
      <Relation @scrollTop="()=>{scrollTop()}" />
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import Search from './Search.vue'
import ListContent from './ListContent.vue'
import Relation from '../base/Relation.vue'
import { watch } from '@vue/runtime-core'
// 控制滚动条回到顶部
const useScrollTop = () => {
  const store = useStore()
  const scrollTop = () => {
    document.querySelector('.rigthcontent_wrapper').scrollTop = store.state.scollElement
  }
  return {
    scrollTop
  }
}
// 监听leftindex的值，控制滚动条
const usechangScroll = () => {
  const store = useStore()
  watch(
    () => store.state.leftMenIndex,
    (val, old) => {
      if (val + 1 === 1) {
        document.querySelector('.rigthcontent_wrapper').scrollTop = 180
      } else {
        document.querySelector('.rigthcontent_wrapper').scrollTop = 300 * val + 180
      }
    }
  )
}
export default {
  name: 'RigthContent',
  setup(props) {
    const { scrollTop } = useScrollTop()
    usechangScroll()
    return {
      scrollTop
    }
  },
  components: {
    Search,
    ListContent,
    Relation
  }
}
</script>

<style lang="scss" scoped>
.rigthcontent_wrapper {
  width: calc(100vw - 2rem);
  height: calc(100vh - 0.61rem);
  overflow-y: scroll;
  .content {
    width: 90%;
    height: 100%;
    // background-color: pink;
    margin: auto;
  }
}
</style>
