<template>
  <div class="wrapper">
    <div class="wrapper_cont">
      <div class="from">
        <div class="from_title">
          <span>登陆账号</span>
        </div>
        <div class="from_content">
          <input
            type="text"
            class="userName base"
            placeholder="用户名"
            v-model.lazy="username"
          />
          <input
            type="password"
            class="paswword base"
            placeholder="密码"
            v-model.lazy="userPassword"
          />
          <div
            class="btn base"
            @click="getUser"
          >立即登录</div>
        </div>
        <div class="bot_tip">
          <span>还没有注册?</span>
          <span>立即注册</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { reactive, toRefs } from 'vue'
import { post } from '../../utils/request'
// 登录逻辑
const useLoginEffect = context => {
  const store = useStore()
  const user = reactive({
    username: '',
    userPassword: ''
  })
  const getUser = async () => {
    if (user.username === '' || user.userPassword === '') {
      alert('请完整输入')
      return true
    }
    try {
      const result = await post('/api/login', {
        username: user.username,
        password: user.userPassword
      })
      if (result?.data?.userInfo?.desc === '成功') {
        context.emit('clickShow', false)
        store.commit('changeIsLogin')
        store.commit('ChangeUserName', user.username)
        user.username = ''
        user.userPassword = ''
      } else if (result?.data?.userInfo?.err === '000') {
        alert('用户名错误，请重新输入')
        user.username = ''
        user.userPassword = ''
      } else if (result?.data?.userInfo?.err === '111') {
        alert('密码错误，请重新输入')
        user.username = ''
        user.userPassword = ''
      }
    } catch (error) {
      console.log('失败')
    }
  }
  const { username, userPassword } = toRefs(user)
  return {
    username,
    userPassword,
    getUser
  }
}

export default {
  name: 'HeaderLogin',
  setup(props, context) {
    const { username, userPassword, getUser } = useLoginEffect(context)
    return {
      username,
      userPassword,
      getUser
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/index.scss';
.wrapper {
  &_cont {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 3.5rem;
    height: 3rem;
    background-color: rgb(255, 255, 255);
    border: 1px solid #aaa;
    z-index: 2;
    .from {
      width: 100%;
      height: 100%;
      position: relative;
      .from_title {
        width: 100%;
        line-height: 0.3rem;
        padding: 0.05rem 0.1rem;
        box-sizing: border-box;
        text-align: left;
        border-bottom: 1px solid #aaa;
        span {
          display: block;
          font-size: 0.16rem;
        }
      }
      .from_content {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        .base {
          width: 90%;
          box-sizing: border-box;
          padding: 0.1rem;
          line-height: 0.1rem;
          font-size: 0.14rem;
          margin-top: 0.25rem;
          border-radius: 0.05rem;
          border: none;
          outline: none;
          &:focus {
            // border: .01rem solid rgb(18, 53, 148);
            box-shadow: 0 0 0.02rem rgb(18, 53, 148);
          }
        }
        .userName,
        .paswword {
          padding-left: 0.15rem;
          border: 1px solid #aaa;
        }
        .btn {
          text-align: center;
          background-color: #28b779;
          line-height: 0.15rem;
          color: #fff;
          cursor: pointer;
        }
      }
      .bot_tip {
        display: flex;
        margin-top: 0.3rem;
        padding-left: 0.16rem;
        span {
          font-size: 0.12rem;
          &:nth-child(2) {
            color: rgb(55, 55, 209);
            cursor: pointer;
          }
        }
      }
    }
  }
}
</style>
