<template>
  <div class="wrapper">
    <div class="menu">
      <div
        v-for="item in list"
        :key="item.id"
        class="menu_item"
      >
        <span
          class="item_title"
          :class="{addclass:indexs==item.id}"
          @click="()=>{handleClickIndex(item.id)}"
        >{{item.name}}</span>
        <ul class="item_content">
          <li
            v-for="(items) in item.content"
            :key="items"
            class="item_content_list"
          >
            <span>{{items.name}}</span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { reactive, toRefs } from '@vue/reactivity'
import { get } from '../../utils/request.js'

// 导航菜单逻辑
const usemenuEffect = () => {
  const content = reactive({ list: [] })
  const getmenu = async () => {
    const result = await get('/api/menu', {})
    content.list = result
  }
  getmenu()
  const { list } = toRefs(content)
  return {
    list
  }
}
// 改变路由并传值,动态添加类名
const useChangIndex = () => {
  const router = useRouter()
  const store = useStore()
  const indexs = store.state.listIndex
  const listName = ['Web', 'Ui', 'Cg', 'Office']
  const handleClickIndex = index => {
    router.push({ name: listName[index] })
    store.commit('changeIndex', { listIndex: index })
  }
  return {
    handleClickIndex,
    indexs
  }
}

export default {
  name: 'ShowMenu',
  props: [],
  setup() {
    const { list } = usemenuEffect()
    const { handleClickIndex, indexs } = useChangIndex()
    return {
      list,
      handleClickIndex,
      indexs
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  position: absolute;
  top: 0.61rem;
  left: 0.1rem;
  width: 9rem;
  height: 4.68rem;
  padding: 0.1rem 0.15rem;
  background-color: rgb(255, 255, 255);
  box-shadow: -0.01rem 0 0.01rem #aaa, 0.01rem 0 0.01rem #aaa, 0 0.01rem 0.01rem #aaa;
  z-index: 1;
  .menu {
    width: 100%;
    height: 100%;
    &_item {
      display: flex;
      margin-bottom: 0.1rem;
      .item_title {
        display: inline-block;
        width: 2rem;
        height: 1rem;
        margin-right: 0.1rem;
        font-size: 0.2rem;
        border: 0.01rem solid rgb(209, 209, 209);
        border-radius: 0.1rem;
        text-align: center;
        line-height: 1rem;
        cursor: pointer;
      }
      .item_content {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        width: 6.8rem;
        margin: 0;
        padding: 0;
        &_list {
          float: left;
          display: flex;
          justify-content: center;
          align-items: center;
          width: 0.8rem;
          height: 0.3rem;
          font-size: 0.12rem;
          list-style: none;
          text-align: center;
          cursor: pointer;
          span {
            display: block;
            padding: 0.03rem 0.06rem;
            border: 0.01rem solid #aaa;
            border-radius: 0.1rem;
            color: #999;
            &:hover {
              background-color: #999;
              color: #fff;
            }
          }
        }
      }
    }
  }
}
.addclass {
  background-color: #aaa;
}
</style>
